html{
  scroll-snap-type: y mandatory;
}
.app{
  display:grid;
  place-items:center;
  height:100vh;
  background-color:black;
}
.app_top{
  margin-bottom:-20px;
}
.app_top>h1{
  text-align:center;
  color:white;
  margin-top:-20px;
}

.app_logo{
  height:12vh
}

.app_videos{
  position:relative;
  height:75vh;
  background-color: white;
  width:70%;
  border-radius:20px;
  max-width:450px;
  max-height:1200px;
  overflow:scroll;
  scroll-snap-type: y mandatory;
}

/* Hide scrollbar for chrome, safari and opera */
.app_videos::-webkit-scrollbar{
  display:none;
}

/* Hide scrollbar for IE, edge and firefox */
.app_videos{
  -ms-overflow-style:none;
  scrollbar-width:none;
}