.videoFooter{
    position:relative;
    margin-left:20px;
    bottom:100px;
}

.videoFooter_text{
    position:absolute;
    bottom:0;
    color:white;
    display:flex;
    margin-bottom:20px;
}

.videoFooter_text>h3{
    margin-left:10px;
    padding-bottom:20px;
}
.videoFooter_text>h3>button{
    color:white;
    font-weight:900;
    text-transform:inherit;
}
.videoFooter_ticker>.ticker{
    height:fit-content;
    width:60%;
    margin-left:30;
    margin-bottom:20px;
}

.videoFooter_ticker h1{
    font-size:12px;
    padding-top:7px;
    color:white;
}
.videoFooter_icon{
    position:absolute;
    left:5px;
    color:white;
}

.videoFooter_actions{
    display:flex;
    position:absolute;
    width:98%;
    justify-content:space-between;
    color:white;
}

.videoFooter_actionsLeft >.MuiSvgIcon-root{         /*this is the class name of icon material ui provides*/
    padding:0 10px;
}

.videoFooter_actionsRight{
    display:flex;
}

.videoFooter_stat{
    display:flex;
    align-items:center;
    margin-right:10px;
}

.videoFooter_stat>p{
    margin-left:3px;
}